<script setup lang="ts">
import Api from '@/api';
import { useLoginStore } from '@/store/session';
import { ref } from 'vue';

// ref
const showConfirmDialog = ref(false);
const loginStore = useLoginStore()

// 
const confirmAction = () => {
  showConfirmDialog.value = true;
};

// 
const executeAction = async () => {
  showConfirmDialog.value = false;
  await Api.reset_score(loginStore.accessToken).then(() => alert('Data was reset!'))
};

// 
const cancelAction = () => {
  showConfirmDialog.value = false;
};

// --- methods ---
</script>

<template>
  <div id="data-reset">
    <h2></h2>
    <div v-if="!showConfirmDialog">
      <button @click="confirmAction" class="btn btn-danger text-nowrap"></button>
    </div>
    <div v-else class="overlay">
      <div class="dialog">
        <p></p>
        <button @click="executeAction" class="btn btn-danger text-nowrap"></button>
        <button @click="cancelAction" class="btn btn-info text-nowrap"></button>
      </div>
    </div>
  </div>
</template>

<style scoped>
#profile-edit {
  padding-top: 20px
}
</style>
