const statsData = computed(() => {
    const logs = [...props.history.logs].sort((a, b) => a.date.localeCompare(b.date))
    const dataMap = new Map()

    for (let i = 0; i < logs.length; i++) {
        const current = logs[i]
        const previous = i > 0 ? logs[i - 1] : null

        // 
        const dailyData = {
            date: current.date,
            // 
            daily: {
