const downloadAsImage = async () => {
    const element = document.getElementById('stats-download-area')
    if (!element) return

    try {
        const canvas = await html2canvas(element, {
            backgroundColor: '#ffffff',
            scale: 2, // 
            useCORS: true,
            logging: false,
            width: 1000, //  (px)
            windowWidth: 1160, // 
